import { Link } from "react-router-dom";
import {useLocation} from 'react-router-dom';
import { useState } from 'react';
import { motion } from "framer-motion";

import './quizPage.css';
import personality from './personality.json';



function ToResult({result, value, lan, sbvalue, nextQuestion}) {

  if(sbvalue === "->") return (
    <>
     <label
        className="label"

        onClick={nextQuestion}
        >
        <h3> {sbvalue} </h3>
      </label>

    </>

  );

  return (
    <>
     <Link to="/ComputerSecurityMBTI-Quiz/result" state={{type:result, num:value, lan:lan, demo:false}} style={{ textDecoration: 'none' }}>
        <motion.button
            className="label"
            whileHover={{ scale: [null, 1.3, 1.2] }}
            transition={{ duration: 0.3 }}
            >
            <h2> {sbvalue} </h2>
        </motion.button>
      </Link> 

    </>

  );
}



function Page({lan, cat, n, handleLeft, handleRight, nextQuestion, lastQ, result, value, sbvalue}) {
  return (
    <>
        
            <div class="question"> <h2> {personality[lan].questions[cat][n]} </h2> </div>
            
                
                {lastQ
                ? <> 
                  <ToResult result={result} value={value} lan={lan} sbvalue={sbvalue} nextQuestion={() => nextQuestion()}  />
                  </>
                : <div class="button_container-quiz"> 
                  <motion.label
                    className="label"
                    whileHover={{ scale: [null, 1.3, 1.2] }}
                    transition={{ duration: 0.3 }}
                    onClick={handleLeft}
                    >
                    <h3> {personality[lan].left[cat][n]} </h3>
                  </motion.label>
                  <motion.label
                    className="label"
                    whileHover={{ scale: [null, 1.3, 1.2] }}
                    transition={{ duration: 0.3 }}
                    onClick={handleRight}
                    >
                    <h3> {personality[lan].right[cat][n]} </h3>
                  </motion.label>
                  {/*<label onClick={handleLeft}> L </label>*/}
                  {/*<label onClick={nextQuestion}> M </label>*/}
                  {/*<label onClick={handleRight}> R </label>*/}
                  </div>
                }
                
                
        
    </>
  );
}



const Quiz=()=> {

  const location = useLocation();
  const language = location.state.lan;

  const [value, setValue] = useState(0);
  const [result, setResult] = useState("");
  const [cat, setCat] = useState("mind");
  const [qIndex, setqIndex] = useState(0);

  const [lastQ, setLastQ] = useState(false);
  const [sbvalue, setSbvalue] = useState("->")
  

  function handleLeft() {
    let b = value - 1;
    setValue(b);
    // console.log("left")
    // console.log(value);
    nextQuestion();
  }

  function handleRight() {
    let a = value + 1;
    setValue(a);
    // console.log(value);
    nextQuestion();
  }



  function nextQuestion() {
    console.log(value)
    console.log(result)
    if(qIndex === 4) {
      if(cat === "mind") {
        // setCat("energy")
        // setqIndex(0)
        setqIndex(qIndex + 1);
        setLastQ(true);

        if(value > 0) setResult(result + "E");
        else setResult(result + "I");
      }
      else if(cat === "energy") {
        // setCat("nature")
        // setqIndex(0)
        setqIndex(qIndex + 1);
        setLastQ(true);

        if(value > 0) setResult(result + "S");
        else setResult(result + "N");
      }
      else if(cat === "nature") {
        // setCat("tactics")
        // setqIndex(0)
        setqIndex(qIndex + 1);
        setLastQ(true);

        if(value > 0) setResult(result + "F");
        else setResult(result + "T");
      }
      else if(cat === "tactics") {
        setLastQ(true)
        if(language === "en") setSbvalue("\"After all, I'm a...\"");
        else setSbvalue("\"...\"")
        setCat("ending")
        setqIndex(0)

        if(value > 0) setResult(result + "P");
        else setResult(result + "J");
      }
      setValue(0)
      return;
    }

    if(qIndex === 5) {
      if(cat === "mind") {
        setCat("energy");
        setqIndex(0);
        setLastQ(false);
      }
      else if(cat === "energy") {
        setCat("nature");
        setqIndex(0);
        setLastQ(false);
      }
      else if(cat === "nature") {
        setCat("tactics");
        setqIndex(0);
        setLastQ(false);
      }
      return;
    }
    setqIndex(qIndex + 1)
  }


  return (
    <>
    {/*<h3> You are a software just executed for the first time on a machine, the owner seems to be an interesting person, let's explore! </h3>*/}
        <div className="Quiz">
            
            <div class="page-wrapper-quiz">

                <div class="content-wrapper-quiz">


                    <Page   lan = {language}
                            cat={cat} 
                            n={qIndex} 
                            handleLeft={() => handleLeft()} 
                            handleRight={() => handleRight()} 
                            nextQuestion={() => nextQuestion()} 
                            lastQ={lastQ} 
                            result={result} 
                            value={value} 
                            sbvalue={sbvalue}
                    />

                </div>
            </div>
        </div>
        </>
        );
}

export default Quiz;
